export class File {
    constructor(id, name, extension, createAt, createBy, modifiedAt, modifiedBy) {
        this.id = id;
        this.name = name;
        this.extension = extension;
        this.createAt = createAt;
        this.createBy = createBy;
        this.modifiedAt = modifiedAt;
        this.modifiedBy = modifiedBy;
    }
}
